.dialogs {
    padding: 0 5px 0 20px;
    display: grid;
    grid-template-columns: 180px 10fr;
}

.dialogs_items {
    padding-top: 20px;
    border-right: 1px solid;
}

.dialogs_items a {
    color: #000;
    margin-left: 10px;
    text-emphasis: none;
    text-decoration: none;
}

.dialogs_items .active {
    color: #ff9e00;

}

.dialogs_items .active img {
    width: 50px;
    height: 50px;
    border-radius: 50px;
}

.message_item {
    display: grid;
    grid-template-columns: 1fr;
}

.messages {
    height: 590px;
    overflow-y: auto;
    margin-bottom: 0;   
}

.myMessage{
    justify-self: end;
    margin-right: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
    color: #ffffff;
    background-color: #2b5378;
    min-width: 90px;
    max-width: 535px;
    display: table;
    padding: 10px;
    border-radius: 10px;
    font-size: 15px;
    position: relative;  
}

.myMessage:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 10px solid #2b5378;
    border-left: 10px solid transparent;
    border-right: 6.5px solid transparent;
    bottom: 0;
    right: -5.5px;
}

.userMessage{
    color: #ffffff;
    background-color: #182533;
    margin-top: 20px;
    margin-left: 20px;
    margin-bottom: 10px;
    margin-right: auto;
    min-width: 90px;
    max-width: 535px;
    display: table;
    padding: 10px;
    border-radius: 10px;
    font-size: 15px;
    position: relative;
}

.userMessage:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    border-bottom: 10px solid #182533;
    border-left: 6px solid transparent;
    border-right: 10px solid transparent;
    bottom: 0;
    left: -5px;
}

.type_message_block {
    background-color: #292e35;
    min-height: 50px;
    max-height: 200px;
    border-left: 1px solid;  
    margin-left: -0.5px;
}

.type_message_block textarea {
    background-color: #292e35;
    resize: none;
    color: #fff;
    border: none;
    outline: none;
    width: 100%;
    max-width: 1000px;
    max-height: 200px;
    padding: 20px 0 0 20px;
}

.type_message_block button {
    float: right;
    background-color: #2b5378;
    color: #fff;
    border: none;
    width: 80px;
    height: 30px;
    border-radius: 6px;
    margin: 12px 20px;
    
}

.messages::-webkit-scrollbar {
    width: 5px;   
}

.messages::-webkit-scrollbar-thumb {
    background: rgba(0,0,0,0.1);
    background-color: darkgrey ;
    border-radius: 7px;    
}


  


